import SortControls from './components/SortControls';
import Link from 'next/link';
import ProductDeleteButton from './components/ProductDeleteButton';

export default async function ProductsPage({ searchParams }) {
  let products = [];
  let error = null;
  const sort = searchParams?.sort || 'createdAt';
  const order = searchParams?.order || 'desc';

  try {
    const res = await fetch(`http://localhost:3000/api/products?sort=${sort}&order=${order}`, { cache: 'no-store' });
    if (!res.ok) {
      throw new Error(`Failed to fetch products: ${res.status}`);
    }
    const data = await res.json();
    products = Array.isArray(data.data) ? data.data : (Array.isArray(data.products) ? data.products : []);
  } catch (err) {
    error = err.message;
  }

  return (
    <div className="py-6 space-y-8">
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-3xl font-bold text-gray-900">Products</h1>
        <Link href="/dashboard/products/new" className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition">Add New Product</Link>
      </div>
      <SortControls />
      {error ? (
        <div className="p-4 text-red-600">Error: {error}</div>
      ) : products.length === 0 ? (
        <div className="text-gray-600">No products found.</div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {products.map(product => (
            <div key={product._id} className="bg-white shadow rounded-lg p-6 flex flex-col">
              <div className="font-semibold text-lg mb-2">{product.name}</div>
              <div className="text-gray-700 mb-1">Price: {product.price?.toLocaleString?.() ?? product.price} Toman</div>
              <div className="text-gray-500 text-sm mb-2">Stock: {product.stock}</div>
              {product.imageUrl && (
                <img src={product.imageUrl} alt={product.name} className="h-32 object-contain mb-2 rounded border" />
              )}
              <div className="text-xs text-gray-400 mb-2">ID: {product._id}</div>
              <div className="flex gap-2 mt-auto">
                <Link href={`/dashboard/products/edit/${product._id}`} className="px-3 py-1 bg-yellow-500 text-white rounded hover:bg-yellow-600 text-center">Edit</Link>
                <ProductDeleteButton productId={product._id} />
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}